






























































import axios from 'axios';
import Vue from 'vue';
import items from '@/components/items.vue';
import loading from '@/components/loading.vue';
import error from '@/components/error.vue';
import searchForm from '@/components/searchForm.vue';
import api from '@/js/api';
import factories from '@/js/factories';

export default Vue.extend({
  name: 'SearchResults',
  components: {
    searchForm,
    items,
    loading,
    error,
  },
  data() {
    return {
      results: [] as string[],
      facets: [] as string[],
      errors: [] as string[],
      loading: true,
    };
  },
  mounted() {
    this.fetchData();
  },
  watch: {
    $route(to, from) {
      this.fetchData();
    },
  },
  methods: {
    async fetchData() {
      const searchStr = this.$route.query;
      this.loading = true;
      this.errors = [];
      try {
        if (Object.keys(searchStr).length > 0) {
          const { data } = await api.search(searchStr);
          this.results = factories.extractRows(data);
          this.facets = data.counts;
        }
      } catch (e) {
        this.errors.push(e);
      } finally {
        this.$emit('show-error', this.errors);
        this.loading = false;
      }
    },
  },
});
