













































































import Vue from 'vue';
import items from '@/components/items.vue';
import url from '@/components/url.vue';
import loading from '@/components/loading.vue';
import error from '@/components/error.vue';
import api from '@/js/api';
import factories from '@/js/factories';

export default Vue.extend({
  name: 'ItemDetail',
  components: {
    error,
    loading,
    items,
    url,
  },
  data() {
    return {
      item: {} as any,
      errors: [] as any,
      relatedItems: [] as any[],
      sidenav: [] as any[],
      loading: true,
    };
  },
  mounted() {
    this.fetchData();
    /*
    return setTimeout(() => {
      this.fetchData();
    }, 3000);
    */
  },
  watch: {
    $route(to, from) {
      this.fetchData();
    },
  },
  methods: {
    async getItem() {
      try {
        const { data } = await api.lookup(this.$route.params.id);
        return data;
      } catch (e) {
        this.errors.push(e);
      }
    },
    async getRelatedPolicies() {
      try {
        const { data } = await api.getItemsById(this.item.policies);
        // https://wesbos.com/destructuring-objects/
        return factories.extractRows(data);
      } catch (e) {
        this.errors.push(e);
      }
    },
    async getRelatedItems() {
      try {
        const { data } = await api.getRelatedItems(this.item._id);
        return factories.extractRows(data);
      } catch (e) {
        this.errors.push(e);
      }
    },
    async getSidenav() {
      try {
        const { data } = await api.getPolicies();
        return factories.extractRows(data);
      } catch (e) {
        this.errors.push(e);
      }
    },
    async fetchData() {
      this.loading = true;
      try {
        this.item = await this.getItem();
        this.sidenav = await this.getSidenav();
        if (this.item.type !== 'policy' && this.item.policies && this.item.policies.length > 0) {
          this.relatedItems = await this.getRelatedPolicies();
        } else {
          this.relatedItems = await this.getRelatedItems();
        }
      } catch (e) {
        this.errors.push(e);
      } finally {
        if (this.errors && this.errors.length > 0) {
          this.$emit('show-error', this.errors);
        }
        this.loading = false;
      }
    },
  },
});
